* {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

#datetime-picker {
  border: 1px solid rgba(0, 0, 0, .5);
  border-radius: 2px;
  margin: 10px 5px 0 30px;
  padding: 4px 12px;
  font-size: 20px;
}

[data-start] {
  border: 1px solid rgba(0, 0, 0, .5);
  border-radius: 2px;
  padding: 4px 12px;
  font-size: 20px;
}

.field {
  flex-direction: column;
  align-items: center;
  margin: 0 11px;
  display: flex;
}

.value {
  font-size: 46px;
  line-height: 42px;
}

.label {
  text-transform: uppercase;
  font-size: 18px;
}

.timer {
  flex-direction: row;
  margin: 40px 20px;
  display: flex;
}

